import React, { Component } from 'react';
import { CAPITALIZE_FIRST_LETTER } from '../../../../Services/Utils/utils';

class Profile extends Component {
    constructor(props) {
        super(props)
        this.state = {
            profileError:false 
        }
    }

    getRandomInt(max) {
        return Math.floor(Math.random() * max);
    }
    render() {
        const {profileError} = this.state;
        const {className, name, profileUrl, network} = this.props;
        const firstLatter = name.charAt(0) //CAPITALIZE_FIRST_LETTER(name)
        return  <div className={className}>
                    {profileError?
                    <span className={`symbol-label text-light-alt fw-bolder fs-5 text-uppercase bg-warning`}>{firstLatter}</span>
                    :    
                    <img src={profileUrl} height="50" width="50" alt={name} onError={()=>this.setState({profileError:true})}/>
                    }
                </div>
    }
}

export default Profile;